import React, { useState } from "react";
import { Box, Button, TextField, Typography, Paper, Fade } from "@mui/material";
import styled from "@emotion/styled";
import Background from "./images/Background.png"; // left background
import BannerImage from "./images/BackgroundMenu.png"; // right banner image
import axios from "axios";
import { useNavigate } from "react-router-dom";

// Full screen flex container
const FullScreenContainer = styled(Box)({
  display: "flex",
  minHeight: "100vh",
  width: "100%",
});

// Left section
const LoginSection = styled(Box)({
  flex: 1,
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  backgroundImage: `url(${Background})`,
  backgroundSize: "cover",
  backgroundPosition: "center",
  backgroundRepeat: "no-repeat",
});

// Right banner section with image
const BannerSection = styled(Box)({
  flex: 1,
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  backgroundImage: `url(${BannerImage})`,
  backgroundSize: "cover",
  backgroundPosition: "center",
  backgroundRepeat: "no-repeat",
  color: "#fff",
  padding: "2rem",
  textAlign: "center",
});

// Paper styling for login form
const StyledPaper = styled(Paper)({
  padding: "2.5rem 2rem",
  borderRadius: "1.5rem",
  boxShadow: "0 8px 32px rgba(31, 41, 55, 0.15)",
  minWidth: 340,
  maxWidth: 400,
  width: "100%",
  backgroundColor: "rgba(255,255,255,0.9)", // slightly transparent for blending
});

// Common styled button for both splash and submit
const StyledButton = styled(Button)({
  borderRadius: "2rem",
  fontWeight: 600,
  textTransform: "none",
  background: "linear-gradient(90deg, #6366f1 0%, #3b82f6 100%)",
  color: "#fff",
  "&:hover": {
    background: "linear-gradient(90deg, #4f46e5 0%, #2563eb 100%)",
  },
});

export default function LoginPage() {
  const [showForm, setShowForm] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [errors, setErrors] = useState({ username: "", password: "" });
  const [errorMessage, setErrorMessage] = useState("");
  const navigate = useNavigate();

  const validate = () => {
    let valid = true;
    let newErrors = { username: "", password: "" };
    if (!username.trim()) {
      newErrors.username = "Username is required";
      valid = false;
    } else if (username.length < 6) {
      newErrors.username = "Username must be at least 6 characters";
      valid = false;
    } else if (username.length > 10) {
      newErrors.username = "Username must be at most 10 characters";
      valid = false;
    }

    if (!password) {
      newErrors.password = "Password is required";
      valid = false;
    } else if (password.length < 8) {
      newErrors.password = "Password must be at least 8 characters";
      valid = false;
    } else if (password.length > 12) {
      newErrors.password = "Password must be at most 12 characters";
      valid = false;
    }

    setErrors(newErrors);
    return valid;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setErrorMessage("");
    if (!validate()) return;

    const data = { name: username, password };

    try {
      const response = await axios.post(
        "http://localhost:8080/user/login",
        data
      );
      if (response.status === 200) {
        navigate("/menu");
        localStorage.setItem("role", response.data.empRole);
        localStorage.setItem("employeeCode", response.data.employeeCode);
        localStorage.setItem("token", "sample-token");
        localStorage.setItem("username", username);
      } else {
        setErrorMessage("Invalid username or password.");
      }
    } catch (err) {
      const msg =
        err.response?.data?.message || "Login failed. Please try again.";
      setErrorMessage(msg);
    }
  };

  return (
    <FullScreenContainer>
      {/* Left: splash or login form */}
      <LoginSection>
        {!showForm ? (
          <StyledButton
            variant="contained"
            size="large"
            onClick={() => setShowForm(true)}
            sx={{ minWidth: 200, minHeight: 50, fontSize: "1.2rem" }}
          >
            Login
          </StyledButton>
        ) : (
          <Fade in={showForm}>
            <StyledPaper elevation={4}>
              <Typography
                variant="h5"
                align="center"
                fontWeight={700}
                gutterBottom
                color="primary"
              >
                Work Buddy HR
              </Typography>
              <Box
                component="form"
                noValidate
                autoComplete="off"
                onSubmit={handleSubmit}
              >
                <TextField
                  label="Username"
                  variant="outlined"
                  fullWidth
                  margin="normal"
                  autoFocus
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  error={!!errors.username}
                  helperText={errors.username}
                  inputProps={{ maxLength: 10 }}
                />
                <TextField
                  label="Password"
                  type="password"
                  variant="outlined"
                  fullWidth
                  margin="normal"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  error={!!errors.password}
                  helperText={errors.password}
                  inputProps={{ maxLength: 12 }}
                />
                {errorMessage && (
                  <Typography
                    color="error"
                    variant="body2"
                    align="center"
                    sx={{ mt: 1 }}
                  >
                    {errorMessage}
                  </Typography>
                )}
                <StyledButton type="submit" fullWidth variant="contained" sx={{ mt: 2 }}>
                  Login
                </StyledButton>
              </Box>
            </StyledPaper>
          </Fade>
        )}
      </LoginSection>

      {/* Right: banner */}
      <BannerSection>
        <Box sx={{ backgroundColor: "rgba(0,0,0,0.4)", borderRadius: "1rem", p: 2 }}>
          <Typography variant="h3" fontWeight={700} gutterBottom>
            Welcome to Work Buddy
          </Typography>
          <Typography variant="h6">
            Streamline your HR tasks and manage attendance, meetings, and more effortlessly.
          </Typography>
        </Box>
      </BannerSection>
    </FullScreenContainer>
  );
}
